
import { computed, defineComponent } from 'vue'

export default defineComponent({
  props: {
    linkIcon: {
      type: String
    }
  },
  setup (props) {
    const tag = computed(() => {
      return props.linkIcon === 'home' ? `${'Account'.toUpperCase()}` : `${'website'.toUpperCase()}`
    })
    return { tag }
  }
})
