
import bus from '@/plugins/bus'
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    isLoading: {
      type: Boolean
    }
  },
  setup () {
    const modalShow = ref(false)
    const modalToggle = () => {
      modalShow.value = !modalShow.value
    }
    bus.on('*', modalToggle)
    return {
      modalShow,
      modalToggle
    }
  }
})
